body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#projects {
  display: flex;
  flex-direction: column;
  padding: 40px;
  align-items: center;
}

#projectDetails {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 2rem;
  padding: 20px;
}

#projects h2 {
  font-size: 2.25rem;
  color: white;
}

.proj {
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(#020202, #181818);
  border-radius: 10px;
  color: white;
  padding: 20px;
  opacity: 0;
  margin-bottom: 20px;
  align-items: center;
  justify-content: center;
  -webkit-animation: fade-in 1s ease-in-out forwards, slide-up 1s ease-in-out forwards;
          animation: fade-in 1s ease-in-out forwards, slide-up 1s ease-in-out forwards;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.proj:hover {
  -webkit-transform: scale(1.05) rotate(5deg);
          transform: scale(1.05) rotate(5deg);
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  to {
    opacity: 0.8;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  to {
    opacity: 0.8;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slide-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    opacity: 0.8;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    opacity: 0.8;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes rotate {
  from {
    opacity: 0;
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
  to {
    opacity: 0.8;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

@keyframes rotate {
  from {
    opacity: 0;
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
  to {
    opacity: 0.8;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

.icons {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.icons img {
  width: 50px;
  height: 50px;
  padding: 5px;
}

.proj h3,
.proj span,
.proj a {
  margin-bottom: 10px;
}

.proj button {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.proj button:hover {
  background-color: #555;
}


@media (max-width: 768px) {
  #projectDetails {
    grid-template-columns: 1fr;
  }
}

#skills {
  display: flex;
  flex-direction: column;
  padding: 40px;
  align-items: center;
}

#skills-grid {
  display: flex;
  padding-top: 20px;
  align-items: center;
  justify-content: center;
}

#skills h2{
    font-size: 2.25rem;
    color: white;
}

.skills-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.skill {
  margin-bottom: 10px;
}

.skill-name {
  font-weight: bold;
  color: #f0f0f0;
}

.skill-bar {
  width: 100%;
  height: 10px;
  background-color: #f0f0f0;
  border-radius: 5px;
}

.skill-type {
  background-color: #fff;
  color: #333;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin: 5px;
  opacity: 0.8;
  transition: opacity 0.3s ease-in-out;
}

.skill-type:hover {
  opacity: 1;
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes dropAnimation1 {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes dropAnimation1 {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
.skill-row {
  display: inline-block;
  -webkit-animation-name: dropAnimation1;
          animation-name: dropAnimation1;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

.skill-row:nth-child(1) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}
.skill-row:nth-child(2) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
.skill-row:nth-child(3) {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}
.skill-row:nth-child(4) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}
.skill-row:nth-child(5) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.skill-row:nth-child(6) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}




#experiences {
  display: flex;
  flex-direction: column;
  padding: 40px;
  justify-content: center;
  align-items: center;
}

.experience-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 0.5rem;
}


#experiences h2 {
  font-size: 2.25rem;
  color: white;
  padding: 20px;
}

.experience {
  display: grid;
  grid-template-columns: 25% 75%;
  grid-gap: 30px;
  gap: 30px;
  padding: 20px;
  border-bottom: 1px solid #ccc;
  position: relative;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.experience:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.experience .year {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.experience .experience-details {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}


.experience p {
  font-size: 16px;
  margin-bottom: 10px;
}

.end {
  margin-top: 20vh;
}

@media screen and (max-width: 768px) {
  .experience-list {
    grid-template-columns: 1fr; /* Change to 1 fraction for mobile */
  }
}


